import React, { FunctionComponent } from 'react';
import Block from 'common/components/block/Block';
import getMessage from 'common/util/i18nUtils';
import { useIntl } from 'react-intl';
import DatoInput from 'app/formik/wrappers/DatoInput';
import InputField from 'app/formik/wrappers/InputField';
import JaNeiSprsml from 'app/formik/wrappers/JaNeiSprsml';
import { Nring } from 'app/types/SelvstendigNringsdrivende';
import Textarea from 'app/formik/wrappers/Textarea';

interface VarigEndringAvNringsinntektProps {
    values: Partial<Nring>;
}

type Props = VarigEndringAvNringsinntektProps;
const VarigEndringAvNringsinntekt: FunctionComponent<Props> = (props: Props) => {
    const intl = useIntl();
    const { values } = props;

    const visDato = values.hattVarigEndringAvNringsinntektSiste4Kalenderr === true;
    const visNringsinntektEtterEndring =
        visDato &&
        values.endringAvNringsinntektInformasjon !== undefined &&
        values.endringAvNringsinntektInformasjon.dato !== undefined;

    const visForklaring =
        visNringsinntektEtterEndring &&
        values.endringAvNringsinntektInformasjon !== undefined &&
        values.endringAvNringsinntektInformasjon.nringsinntektEtterEndring !== undefined;

    return (
        <>
            <Block>
                <JaNeiSprsml
                    name="hattVarigEndringAvNringsinntektSiste4Kalenderr"
                    legend={getMessage(
                        intl,
                        'arbeidsforhold.selvstendig.endringAvNringsinntektInformasjon.hattVarigEndringAvNringsinntektSiste4Kalenderr'
                    )}
                />
            </Block>

            <Block visible={visDato}>
                <DatoInput
                    fullskjermKalender={true}
                    name="endringAvNringsinntektInformasjon.dato"
                    label={getMessage(intl, 'arbeidsforhold.selvstendig.endringAvNringsinntektInformasjon.dato')}
                />
            </Block>

            <Block visible={visNringsinntektEtterEndring}>
                <InputField
                    name="endringAvNringsinntektInformasjon.nringsinntektEtterEndring"
                    label={getMessage(
                        intl,
                        'arbeidsforhold.selvstendig.endringAvNringsinntektInformasjon.nringsinntektEtterEndring'
                    )}
                    required={true}
                />
            </Block>

            <Block visible={visForklaring}>
                <Textarea
                    name="endringAvNringsinntektInformasjon.forklaring"
                    label={getMessage(intl, 'arbeidsforhold.selvstendig.endringAvNringsinntektInformasjon.forklaring')}
                    maxLength={1000}
                />
            </Block>
        </>
    );
};

export default VarigEndringAvNringsinntekt;
