import React, { FunctionComponent } from 'react';
import Block from 'common/components/block/Block';
import { Nring } from 'app/types/SelvstendigNringsdrivende';
import EndringSelvstendig from './selvstendig/EndringSelvstendig';
import RevisorSelvstendig from './selvstendig/RevisorSelvstendig';
import RegnskapsfrerSelvstendig from './selvstendig/RegnskapsfrerSelvstendig';
import DetaljerSelvstendig from './selvstendig/DetaljerSelvstendig';
import DuHarSvartNeiListe from '../DuHarSvartNeiListe';

interface Props {
    selvstendigInformasjon: Nring;
}

const InformasjonOmSelvstendig: FunctionComponent<Props> = ({ selvstendigInformasjon }) => {
    return (
        <Block margin="xxs">
            <div className="grayInfoBox">
                <DetaljerSelvstendig
                    navnPNringen={selvstendigInformasjon.navnPNringen}
                    orgnr={selvstendigInformasjon.organisasjonsnummer}
                    oppstartsdato={selvstendigInformasjon.oppstartsdato!}
                    pgende={selvstendigInformasjon.pgende}
                    tidsperiode={selvstendigInformasjon.tidsperiode}
                    typer={selvstendigInformasjon.nringstyper}
                />

                {selvstendigInformasjon.endringAvNringsinntektInformasjon && (
                    <EndringSelvstendig
                        endringAvNringsinntektInformasjon={selvstendigInformasjon.endringAvNringsinntektInformasjon}
                    />
                )}

                {selvstendigInformasjon.harRevisor && <RevisorSelvstendig revisor={selvstendigInformasjon.revisor!} />}

                {selvstendigInformasjon.harRegnskapsfrer && (
                    <RegnskapsfrerSelvstendig regnskapsfrer={selvstendigInformasjon.regnskapsfrer!} />
                )}

                <DuHarSvartNeiListe
                    selvstendigOppsummering={true}
                    endringAvNringsinntekt={selvstendigInformasjon.hattVarigEndringAvNringsinntektSiste4Kalenderr}
                    harBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene={
                        selvstendigInformasjon.harBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene
                    }
                    harRegnskapsfrer={selvstendigInformasjon.harRegnskapsfrer}
                    harRevisor={selvstendigInformasjon.harRevisor}
                    kanInnhenteOpplsyningerFraRevisor={selvstendigInformasjon.kanInnhenteOpplsyningerFraRevisor}
                />
            </div>
        </Block>
    );
};

export default InformasjonOmSelvstendig;
