import Arbeidsforhold from './Arbeidsforhold';

export default interface Bankkonto {
    kontonummer: string;
    banknavn: string;
}

export enum Kjnn {
    'MANN' = 'M',
    'KVINNE' = 'K',
}

export interface PersonBase {
    fnr: string;
    fornavn: string;
    mellomnavn?: string;
    etternavn: string;
    kjnn: Kjnn;
    fdselsdato: Date;
}

export interface Person extends PersonBase {
    land: string;
    ikkeNordiskEsLand: boolean;
    erMyndig: boolean;
    bankkonto?: Bankkonto;
}

export interface Skerinfo {
    sker: Person;
    arbeidsforhold: Arbeidsforhold[];
}
