import React, { FunctionComponent } from 'react';
import { connect } from 'react-redux';

import { getData } from 'app/utils/fromFetchState';
import { Undertittel } from 'nav-frontend-typografi';
import { Skerinfo } from 'app/types/Skerinfo';
import { State } from 'app/redux/store';
import Applikasjonsside from '../applikasjonsside/Applikasjonsside';
import Block from 'common/components/block/Block';
import FetchState from 'app/types/FetchState';
import Kvittering from 'app/types/Kvittering';
import Panel from 'nav-frontend-paneler';
import './sknadSendt.less';
import StatusBoks from './components/StatusBoks';
import KvitteringHeader from './components/KvitteringHeader';
import { FormattedMessage } from 'react-intl';
import KvitteringSuksess from './components/KvitteringSuksess';
import BEMHelper from 'common/util/bem';
import HvorLengeIkon from 'app/icons/HvorLenge';

const cls = BEMHelper('sknadSendt');

interface Props {
    kvittering: FetchState<Kvittering>;
    skerinfo: FetchState<Skerinfo>;
}

const SknadSendt: FunctionComponent<Props> = ({ kvittering, skerinfo }) => {
    const { mottattDato, pdf, saksNr } = getData(kvittering, {});
    const { sker } = getData(skerinfo, {});

    return (
        <Applikasjonsside visTittel={true}>
            <div className={cls.block}>
                <KvitteringHeader sker={sker} mottattDato={mottattDato} pdf={pdf} />

                <KvitteringSuksess />

                <StatusBoks saksNr={saksNr} />

                <Block>
                    <Undertittel className={cls.element('tittel')}>
                        <FormattedMessage id="sknadSendt.foreldrepenger" />
                    </Undertittel>
                </Block>

                <Block>
                    <Panel className={cls.element('foreldrepengerPanel')}>
                        <div>
                            <Block margin="xxs">
                                <Undertittel>
                                    <FormattedMessage id="sknadSendt.foreldrepenger.boks.tittel" />
                                </Undertittel>
                            </Block>
                            <Block margin="xs">
                                <FormattedMessage id="sknadSendt.foreldrepenger.boks.innhold" />
                            </Block>
                        </div>
                        <div className={cls.element('ikon')}>
                            <HvorLengeIkon />
                        </div>
                    </Panel>
                </Block>
            </div>
        </Applikasjonsside>
    );
};

const mapStateToProps = (state: State) => ({
    skerinfo: state.api.skerinfo,
    kvittering: state.api.kvittering,
});

export default connect(mapStateToProps)(SknadSendt);
