import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { useIntl, FormattedMessage } from 'react-intl';
import { Kjnn, Skerinfo } from 'app/types/Skerinfo';
import FetchState, { FetchStatus } from 'app/types/FetchState';

import { ApiActionTypes } from 'app/redux/types/ApiAction';
import { getData, getErrorCode } from 'app/utils/fromFetchState';
import { State } from 'app/redux/store';
import Action from 'app/redux/types/Action';
import Environment from 'app/Environment';
import Feil from 'app/components/Feil';
import FormikWrapper from '../formik-wrapper/FormikWrapper';
import getMessage from 'common/util/i18nUtils';
import Kvittering from 'app/types/Kvittering';
import Loading from 'app/components/loading/Loading';
import SknadRoutes from '../sknad-routes/SknadRoutes';

interface Props {
    skerinfo: FetchState<Skerinfo>;
    kvittering: FetchState<Kvittering>;
    requestSkerinfo: () => void;
}

const Svangerskapspengesknad: React.FunctionComponent<Props> = (props) => {
    const intl = useIntl();
    const { skerinfo, kvittering, requestSkerinfo } = props;

    useEffect(() => {
        if (skerinfo.status === FetchStatus.UNFETCHED) {
            requestSkerinfo();
        }

        if (getErrorCode(skerinfo) === 401 || getErrorCode(skerinfo) === 403) {
            redirectToLogin();
        }
    });

    const redirectToLogin = () => {
        window.location.href = Environment.LOGIN_URL + '?redirect=' + window.location.href;
    };

    const sker = getData(skerinfo, {}).sker;
    const isLoading =
        skerinfo.status === FetchStatus.IN_PROGRESS ||
        skerinfo.status === FetchStatus.UNFETCHED ||
        kvittering.status === FetchStatus.IN_PROGRESS ||
        getErrorCode(skerinfo) === 401;

    if (isLoading) {
        return <Loading />;
    } else if (sker && sker.kjnn === Kjnn.MANN) {
        return (
            <Feil
                tittel={getMessage(intl, 'feilside.mann.tittel')}
                melding={getMessage(intl, 'feilside.mann.melding')}
            />
        );
    } else if (kvittering.status === FetchStatus.FAILURE && getErrorCode(kvittering) !== 401) {
        return (
            <Feil
                tittel={getMessage(intl, 'feilside.innsending.tittel')}
                melding={
                    <FormattedMessage
                        id={'feilside.innsending.melding'}
                        values={{
                            a: (msg: any) => (
                                <a
                                    href="https://www.nav.no/no/NAV+og+samfunn/Kontakt+NAV/Teknisk+brukerstotte/hjelp-til-personbruker?kap=398749"
                                    target="_blank"
                                    rel="noreferrer"
                                    className="lenke"
                                >
                                    {msg}
                                </a>
                            ),
                        }}
                    />
                }
            />
        );
    } else if (skerinfo.status === FetchStatus.FAILURE && getErrorCode(skerinfo) !== 401) {
        return (
            <Feil
                tittel={getMessage(intl, 'feilside.ikkeTilgjengelig.tittel')}
                melding={getMessage(intl, 'feilside.ikkeTilgjengelig.melding')}
            />
        );
    } else {
        return (
            <FormikWrapper
                contentRenderer={(formikProps) => (
                    <SknadRoutes
                        formikProps={formikProps}
                        harSendtSknad={kvittering.status !== FetchStatus.UNFETCHED}
                    />
                )}
            />
        );
    }
};

const mapStateToProps = (state: State) => ({
    skerinfo: state.api.skerinfo,
    kvittering: state.api.kvittering,
});

const mapDispatchToProps = (dispatch: (action: Action) => void) => ({
    requestSkerinfo: () => {
        dispatch({ type: ApiActionTypes.GET_SKERINFO_REQUEST });
    },
});

export default connect(mapStateToProps, mapDispatchToProps)(Svangerskapspengesknad);
