import React, { FunctionComponent } from 'react';
import { connect } from 'react-redux';
import moment from 'moment';

import { ApiActionTypes } from 'app/redux/types/ApiAction';
import { Attachment } from 'common/storage/attachment/types/Attachment';
import { CustomFormikProps } from 'app/types/Formik';
import { FetchStatus } from 'app/types/FetchState';
import { FormattedMessage, useIntl } from 'react-intl';
import { processUtfyltSknad } from 'app/utils/sknadUtils';
import { Skerinfo } from 'app/types/Skerinfo';
import { State } from 'app/redux/store';
import Action from 'app/redux/types/Action';
import Applikasjonsside from '../applikasjonsside/Applikasjonsside';
import BekreftCheckboksPanel from 'app/formik/wrappers/BekreftCheckboksPanel';
import Block from 'common/components/block/Block';
import FormikStep from 'app/components/formik-step/FormikStep';
import getMessage from 'common/util/i18nUtils';
import Oppsummeringspunkt from './components/Oppsummeringspunkt';
import SknadStep from 'app/types/SknadStep';
import Veilederinfo from 'common/components/veileder-info/Veilederinfo';
import Tilrettelegging from 'app/types/Tilrettelegging';
import Arbeidsforhold from 'app/types/Arbeidsforhold';
import TilretteleggingOppsummering from './components/tilrettelegging/TilretteleggingOppsummering';
import ArbeidsforholdOppsummering from './components/arbeidsforhold/ArbeidsforholdOppsummering';
import MedlemskapOppsummering from './components/medlemskap/MedlemskapOppsummering';
import TerminOppsummering from './components/termin/TerminOppsummering';
import SknadDTO from '../../types/S\u00F8knad';
import { isAttachmentWithError } from 'common/storage/attachment/components/util';
import { getAktiveArbeidsforhold } from 'app/utils/arbeidsforholdUtils';
import { Sprkkode } from 'common/types';
import dayjs from 'dayjs';

import './oppsummering.less';

interface OwnProps {
    step: SknadStep;
    formikProps: CustomFormikProps;
    sprkkode: Sprkkode;
}

interface StateProps {
    vedlegg: Attachment[];
    skerinfo: Skerinfo | undefined;
    arbeidsforhold: Arbeidsforhold[];
    requestSendSknad: (sknad: SknadDTO) => void;
}

type Props = OwnProps & StateProps;

const Oppsummering: FunctionComponent<Props> = (props) => {
    const intl = useIntl();
    const { step, vedlegg, skerinfo, arbeidsforhold, requestSendSknad, formikProps, sprkkode } = props;
    const { values } = formikProps;

    const visAdvarselOmManglendeDokumentasjon = values.tilrettelegging.some(
        (t: Tilrettelegging) => t.vedlegg.length === 0
    );

    const sendSknad = () => {
        const ferdigSknad = processUtfyltSknad(values, vedlegg, arbeidsforhold, sprkkode);
        if (ferdigSknad) {
            requestSendSknad(ferdigSknad);
        }
    };

    if (skerinfo === undefined) {
        return null;
    }

    return (
        <Applikasjonsside visTittel={true}>
            <FormikStep
                step={step}
                formikProps={formikProps}
                showNesteknapp={true}
                onValidFormSubmit={sendSknad}
            >
                <Block>
                    <Veilederinfo visVeileder={true} stil="kompakt" type="info">
                        <FormattedMessage id="oppsummering.veileder" />
                    </Veilederinfo>
                </Block>
                <Oppsummeringspunkt
                    type="termin"
                    title={getMessage(intl, 'oppsummering.termin.tittel.barnetErIkkeFdt')}
                >
                    <TerminOppsummering
                        fornavn={skerinfo.sker.fornavn}
                        etternavn={skerinfo.sker.etternavn}
                        fnr={skerinfo.sker.fnr}
                    />
                </Oppsummeringspunkt>
                <Oppsummeringspunkt type="barn" title={getMessage(intl, 'oppsummering.barn.tittel')}>
                    <Block margin="xxs">
                        <FormattedMessage
                            id="oppsummering.barn.termindato"
                            values={{
                                dato: moment(values.barn.termindato).format('dddd Do MMMM YYYY'),
                            }}
                        />
                    </Block>
                    <div>
                        {values.barn.fdselsdato !== undefined && (
                            <FormattedMessage
                                id="oppsummering.barn.fdselsdato"
                                values={{ dato: moment(values.barn.fdselsdato).format('dddd Do MMMM YYYY') }}
                            />
                        )}
                    </div>
                </Oppsummeringspunkt>
                <Oppsummeringspunkt
                    type="arbeidsforhold"
                    title={getMessage(intl, 'oppsummering.arbeidsforhold.tittel')}
                >
                    <ArbeidsforholdOppsummering
                        arbeidsforhold={getAktiveArbeidsforhold(
                            arbeidsforhold,
                            dayjs(formikProps.values.barn.termindato).toDate()
                        )}
                        frilansInformasjon={values.sker.frilansInformasjon}
                        selvstendigInformasjon={values.sker.selvstendigNringsdrivendeInformasjon}
                        sknadsgrunnlag={values.sknadsgrunnlag}
                        andreInntekter={values.sker.andreInntekterSiste10Mnd}
                        harHattAndreInntektskilder={values.sker.harHattAnnenInntektSiste10Mnd!}
                        harJobbetFrilans={values.sker.harJobbetSomFrilansSiste10Mnd!}
                        harJobbetSomSelvstendigNringsdrivende={
                            values.sker.harJobbetSomSelvstendigNringsdrivendeSiste10Mnd!
                        }
                    />
                </Oppsummeringspunkt>
                <Oppsummeringspunkt
                    type="tilrettelegging"
                    title={getMessage(intl, 'oppsummering.tilrettelegging.tittel')}
                >
                    <TilretteleggingOppsummering
                        tilrettelegging={values.tilrettelegging}
                        arbeidsforhold={getAktiveArbeidsforhold(
                            arbeidsforhold,
                            dayjs(formikProps.values.barn.termindato).toDate()
                        )}
                    />
                </Oppsummeringspunkt>
                <Oppsummeringspunkt type="medlemskap" title={getMessage(intl, 'oppsummering.medlemskap.tittel')}>
                    <MedlemskapOppsummering
                        iNorgeNeste12Mnd={values.informasjonOmUtenlandsopphold.iNorgeNeste12Mnd!}
                        iNorgeSiste12Mnd={values.informasjonOmUtenlandsopphold.iNorgeSiste12Mnd!}
                        senereOpphold={values.informasjonOmUtenlandsopphold.senereOpphold}
                        tidligereOpphold={values.informasjonOmUtenlandsopphold.tidligereOpphold}
                    />
                </Oppsummeringspunkt>
                <Block visible={visAdvarselOmManglendeDokumentasjon}>
                    <Veilederinfo stil="kompakt" type="advarsel">
                        <FormattedMessage id="oppsummering.veileder.dokumentasjon" />
                    </Veilederinfo>
                </Block>
                <Block>
                    <BekreftCheckboksPanel
                        name="harGodkjentOppsummering"
                        label={getMessage(intl, 'oppsummering.harGodkjentVilkr')}
                    />
                </Block>
            </FormikStep>
        </Applikasjonsside>
    );
};

const mapStateToProps = (state: State) => ({
    skerinfo: state.api.skerinfo.status === FetchStatus.SUCCESS ? state.api.skerinfo.data : undefined,
    arbeidsforhold: state.api.skerinfo.status === FetchStatus.SUCCESS ? state.api.skerinfo.data.arbeidsforhold : [],
    vedlegg: state.attachment.vedlegg.filter((a) => !isAttachmentWithError(a)),
    sprkkode: state.common.sprkkode,
});

const mapDispatchToProps = (dispatch: (action: Action) => void) => ({
    requestSendSknad: (sknad: SknadDTO) => {
        dispatch({ type: ApiActionTypes.SEND_SKNAD_REQUEST, payload: { sknad } });
    },
});

export default connect(mapStateToProps, mapDispatchToProps)(Oppsummering);
