import { UferdigSknad, Sknadfeil } from 'app/types/Sknad';

const validateIntro = (sknad: UferdigSknad): Sknadfeil => {
    const errors: Sknadfeil = {};

    if (sknad.harGodkjentVilkr === false) {
        errors.harGodkjentVilkr = 'valideringsfeil.vilkrMGodkjennes';
    }

    return errors;
};

export default validateIntro;
