import Arbeidsforhold from 'app/types/Arbeidsforhold';
import { Arbeidsforholdstype } from 'app/types/Tilrettelegging';
import Sker from 'app/types/Sker';
import { SknadsgrunnlagOption } from '../../../formik/wrappers/VelgSknadsgrunnlag';
import { useIntl } from 'react-intl';
import { getAnnenInntektElementTitle, getUnikeArbeidsforhold } from '../../../utils/arbeidsforholdUtils';
import { AnnenInntektType } from '../../../types/AnnenInntekt';

export const mapArbeidsforholdToSknadsgrunnlagOptions = (
    sker: Partial<Sker>,
    arbeidsforhold: Arbeidsforhold[],
    termindato: string
    //intl: intl
): SknadsgrunnlagOption[] => {
    const intl = useIntl();
    const { selvstendigNringsdrivendeInformasjon = [], andreInntekterSiste10Mnd = [], frilansInformasjon } = sker;
    const frstegangstjeneste = andreInntekterSiste10Mnd.find(
        (inntekt) => inntekt.type === AnnenInntektType.MILITRTJENESTE
    );
    const unikeArbeidsforhold = getUnikeArbeidsforhold(arbeidsforhold, termindato);

    return [
        ...unikeArbeidsforhold.map((forhold) => ({
            value: forhold.guid,
            label: forhold.arbeidsgiverNavn || 'privat arbeidsgiver',
            type: forhold.arbeidsgiverIdType === 'orgnr' ? Arbeidsforholdstype.VIRKSOMHET : Arbeidsforholdstype.PRIVAT,
        })),
        ...selvstendigNringsdrivendeInformasjon.map((nring) => ({
            value: nring.organisasjonsnummer || `${nring.navnPNringen}${nring.registrertILand}`,
            label: nring.navnPNringen,
            type: Arbeidsforholdstype.SELVSTENDIG,
        })),
        ...(frstegangstjeneste
            ? [
                  {
                      value: frstegangstjeneste.type,
                      label: getAnnenInntektElementTitle(frstegangstjeneste, intl),
                      type: Arbeidsforholdstype.PRIVAT,
                  },
              ]
            : []),
        ...(frilansInformasjon !== undefined
            ? [
                  {
                      value: 'Frilans',
                      label: 'Frilans',
                      type: Arbeidsforholdstype.FRILANSER,
                  },
              ]
            : []),
    ];
};
