import moment from 'moment';
import { isEmpty } from 'lodash';
import { UferdigSknad, Sknadfeil } from 'app/types/Sknad';
import {
    dagenEtter,
    niMnederFremITid,
    etrSiden,
    enMnedSiden,
    halvannetrSiden,
    isInvalidDateString,
} from '../../../common/util/datoUtils';

const validateTermin = (sknad: UferdigSknad): Sknadfeil => {
    const errors: Sknadfeil = {};
    let barn = {};
    let fdselsdatoFeil = {};
    let termindatoFeil = {};

    if (sknad.barn.fdselsdato) {
        if (isInvalidDateString(sknad.barn.fdselsdato)) {
            fdselsdatoFeil = {
                fdselsdato: { intlKey: 'valideringsfeil.ugyldigDato', values: { feltNavn: 'Fdselsdato' } },
            };
        } else {
            if (!moment(sknad.barn.fdselsdato).isBefore(dagenEtter(new Date()))) {
                fdselsdatoFeil = {
                    fdselsdato: 'valideringsfeil.fdselsdatoMVreTilbakeITid',
                };
            }
            if (moment(sknad.barn.fdselsdato).isBefore(halvannetrSiden(new Date()))) {
                fdselsdatoFeil = {
                    fdselsdato: 'valideringsfeil.forLangtTilbakeITid',
                };
            }
        }
    }

    if (sknad.barn.termindato === undefined) {
        termindatoFeil = {
            termindato: 'valideringsfeil.termindatoErPkrevd',
        };
    }

    if (sknad.barn.termindato) {
        if (isInvalidDateString(sknad.barn.termindato)) {
            termindatoFeil = {
                termindato: { intlKey: 'valideringsfeil.ugyldigDato', values: { feltNavn: 'Termindato' } },
            };
        } else {
            if (moment(sknad.barn.termindato).isSameOrAfter(niMnederFremITid(new Date()))) {
                termindatoFeil = {
                    termindato: 'valideringsfeil.forLangtFremITid',
                };
            }
            if (moment(sknad.barn.termindato).isBefore(etrSiden(new Date()))) {
                termindatoFeil = {
                    termindato: 'valideringsfeil.forLangtTilbakeITid',
                };
            }
            if (
                moment(sknad.barn.termindato).isBefore(enMnedSiden(new Date())) &&
                sknad.barn.fdselsdato === undefined
            ) {
                termindatoFeil = {
                    fdselsdato: 'valideringsfeil.vennligstOppgiBarnetsFdselsDato',
                };
            }
        }
    }

    barn = { ...termindatoFeil, ...fdselsdatoFeil };

    if (!isEmpty(barn)) {
        errors.barn = barn;
    }

    return errors;
};

export default validateTermin;
