import * as React from 'react';
import { useIntl } from 'react-intl';
import Block from 'common/components/block/Block';
import InputField from 'app/formik/wrappers/InputField';
import JaNeiSprsml from 'app/formik/wrappers/JaNeiSprsml';
import getMessage from 'common/util/i18nUtils';
import { Nring, Nringsrelasjon as NringsrelasjonType } from 'app/types/SelvstendigNringsdrivende';

interface NringsrelasjonBolkProps {
    values: Nring;
    type: 'revisor' | 'regnskapsfrer';
}

type Props = NringsrelasjonBolkProps;

const Nringsrelasjon: React.FunctionComponent<Props> = (props: Props) => {
    const intl = useIntl();
    const { values, type } = props;
    const nringsrelasjon: Partial<NringsrelasjonType> = values[type] || {};

    const skalViseTelefonnummer = nringsrelasjon.navn !== undefined && nringsrelasjon.navn !== '';
    const skalViseErNrVennEllerFamilie =
        skalViseTelefonnummer && nringsrelasjon.telefonnummer !== undefined && nringsrelasjon.telefonnummer !== '';

    return (
        <>
            <Block>
                <InputField
                    name={`${type}.navn`}
                    label={getMessage(intl, `arbeidsforhold.selvstendig.nringsrelasjon.${type}.navn`)}
                />
            </Block>
            <Block visible={skalViseTelefonnummer}>
                <InputField
                    name={`${type}.telefonnummer`}
                    label={getMessage(intl, `arbeidsforhold.selvstendig.nringsrelasjon.${type}.tlfnr`)}
                />
            </Block>
            <Block visible={skalViseErNrVennEllerFamilie}>
                <JaNeiSprsml
                    name={`${type}.erNrVennEllerFamilie`}
                    legend={getMessage(intl, `arbeidsforhold.selvstendig.nringsrelasjon.erNrVennEllerFamilie`)}
                />
            </Block>
        </>
    );
};
export default Nringsrelasjon;
