import { TidsperiodeMedValgfriSluttdato, TidsperiodeMedValgfriSluttdatoDTO } from 'common/types';

export enum Nringstype {
    'FISKER' = 'FISKE',
    'JORDBRUK' = 'JORDBRUK_SKOGBRUK',
    'DAGMAMMA' = 'DAGMAMMA',
    'ANNET' = 'ANNEN',
}

export interface Nring {
    nringstyper: Nringstype[];
    tidsperiode: Partial<TidsperiodeMedValgfriSluttdato>;
    nringsinntekt: string;
    pgende: boolean;
    navnPNringen: string;
    organisasjonsnummer: string;
    registrertINorge: boolean;
    registrertILand?: string;
    stillingsprosent?: string;
    harBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene?: boolean;
    oppstartsdato?: string;
    hattVarigEndringAvNringsinntektSiste4Kalenderr?: boolean;
    endringAvNringsinntektInformasjon?: EndringAvNringsinntektInformasjon;
    harRegnskapsfrer: boolean;
    regnskapsfrer?: Nringsrelasjon;
    harRevisor: boolean;
    revisor?: Nringsrelasjon;
    kanInnhenteOpplsyningerFraRevisor?: boolean;
}

export interface EndringAvNringsinntektInformasjon {
    dato: string;
    nringsinntektEtterEndring: string;
    forklaring: string;
}

export interface Nringsrelasjon {
    navn: string;
    telefonnummer: string;
    erNrVennEllerFamilie: boolean;
}

export interface NringDTO {
    nringstyper: Nringstype[];
    tidsperiode: Partial<TidsperiodeMedValgfriSluttdatoDTO>;
    nringsinntekt: string;
    pgende: boolean;
    navnPNringen: string;
    organisasjonsnummer: string;
    registrertINorge: boolean;
    registrertILand?: string;
    stillingsprosent?: string;
    harBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene?: boolean;
    oppstartsdato?: Date;
    hattVarigEndringAvNringsinntektSiste4Kalenderr?: boolean;
    endringAvNringsinntektInformasjon?: EndringAvNringsinntektInformasjonDTO;
    harRegnskapsfrer: boolean;
    regnskapsfrer?: Nringsrelasjon;
    harRevisor: boolean;
    revisor?: Nringsrelasjon;
    kanInnhenteOpplsyningerFraRevisor?: boolean;
}

export interface EndringAvNringsinntektInformasjonDTO {
    dato: Date;
    nringsinntektEtterEndring: string;
    forklaring: string;
}
