import { Skerinfo } from 'app/types/Skerinfo';
import ApiAction, { ApiActionTypes } from '../types/ApiAction';
import FetchState, { FetchStatus } from 'app/types/FetchState';
import Kvittering from 'app/types/Kvittering';

export interface ApiState {
    skerinfo: FetchState<Skerinfo>;
    kvittering: FetchState<Kvittering>;
}

const getDefaultState = (): ApiState => ({
    skerinfo: {
        status: FetchStatus.UNFETCHED,
    },
    kvittering: {
        status: FetchStatus.UNFETCHED,
    },
});

const apiReducer = (state = getDefaultState(), action: ApiAction): ApiState => {
    switch (action.type) {
        case ApiActionTypes.GET_SKERINFO_REQUEST:
            return {
                ...state,
                skerinfo: {
                    status: FetchStatus.IN_PROGRESS,
                },
            };

        case ApiActionTypes.GET_SKERINFO_SUCCESS:
            return {
                ...state,
                skerinfo: {
                    status: FetchStatus.SUCCESS,
                    data: {
                        ...action.payload.skerinfo,
                    },
                },
            };

        case ApiActionTypes.GET_SKERINFO_FAILURE:
            return {
                ...state,
                skerinfo: {
                    status: FetchStatus.FAILURE,
                    error: action.payload.error,
                },
            };

        case ApiActionTypes.SEND_SKNAD_REQUEST:
            return {
                ...state,
                kvittering: {
                    status: FetchStatus.IN_PROGRESS,
                },
            };

        case ApiActionTypes.SEND_SKNAD_SUCCESS:
            return {
                ...state,
                kvittering: {
                    status: FetchStatus.SUCCESS,
                    data: action.payload.kvittering,
                },
            };

        case ApiActionTypes.SEND_SKNAD_FAILURE:
            return {
                ...state,
                kvittering: {
                    status: FetchStatus.FAILURE,
                    error: action.payload.error,
                },
            };

        default: {
            return state;
        }
    }
};

export default apiReducer;
