import { Skerinfo } from 'app/types/Skerinfo';
import Kvittering from 'app/types/Kvittering';
import { FetchError } from 'app/types/FetchState';
import SknadDTO from '../../types/Sknad';

export enum ApiActionTypes {
    'GET_SKERINFO_REQUEST' = 'getSkerInfoRequest',
    'GET_SKERINFO_SUCCESS' = 'getSkerInfoSuccess',
    'GET_SKERINFO_FAILURE' = 'getSkerInfoFailure',
    'SEND_SKNAD_REQUEST' = 'sendSknadRequest',
    'SEND_SKNAD_SUCCESS' = 'sendSknadSuccess',
    'SEND_SKNAD_FAILURE' = 'sendSknadFailure',
}

export interface GetSkerinfoRequest {
    type: ApiActionTypes.GET_SKERINFO_REQUEST;
}

export interface GetSkerinfoSuccess {
    type: ApiActionTypes.GET_SKERINFO_SUCCESS;
    payload: {
        skerinfo: Skerinfo;
    };
}

export interface GetSkerinfoFailure {
    type: ApiActionTypes.GET_SKERINFO_FAILURE;
    payload: {
        error: FetchError;
    };
}

export interface SendSknadRequest {
    type: ApiActionTypes.SEND_SKNAD_REQUEST;
    payload: {
        sknad: SknadDTO;
    };
}

export interface SendSknadSuccess {
    type: ApiActionTypes.SEND_SKNAD_SUCCESS;
    payload: {
        kvittering: Kvittering;
    };
}

export interface SendSknadFailure {
    type: ApiActionTypes.SEND_SKNAD_FAILURE;
    payload: {
        error: FetchError;
    };
}

type ApiAction =
    | GetSkerinfoRequest
    | GetSkerinfoSuccess
    | GetSkerinfoFailure
    | SendSknadRequest
    | SendSknadSuccess
    | SendSknadFailure;

export default ApiAction;
