import { Attachment } from 'common/storage/attachment/types/Attachment';
import { TidsperiodeMedValgfriSluttdato } from 'common/types';

export enum AnnenInntektType {
    'MILITRTJENESTE' = 'MILITR_ELLER_SIVILTJENESTE',
    'JOBB_I_UTLANDET' = 'JOBB_I_UTLANDET',
}

abstract class AnnenInntektBase {
    // @ts-ignore Fiks
    tidsperiode: TidsperiodeMedValgfriSluttdato;
    // @ts-ignore Fiks
    pgende: boolean;
    // @ts-ignore Fiks
    vedlegg: Attachment[];
}

export class MilitrtjenesteInntekt extends AnnenInntektBase {
    // @ts-ignore Fiks
    type: AnnenInntektType.MILITRTJENESTE;
}

export class JobbIUtlandetInntekt extends AnnenInntektBase {
    // @ts-ignore Fiks
    type: AnnenInntektType.JOBB_I_UTLANDET;
    // @ts-ignore Fiks
    arbeidsgiverNavn: string;
    // @ts-ignore Fiks
    land: string;
}

export type AnnenInntekt = MilitrtjenesteInntekt | JobbIUtlandetInntekt;

export interface AnnenInntektPartialInterface {
    type: AnnenInntektType;
    tidsperiode: Partial<TidsperiodeMedValgfriSluttdato>;
    vedlegg: Attachment[];
}
