import React, { FunctionComponent } from 'react';
import { Navigate, Route, Routes } from 'react-router';

import { AppRoute } from 'app/types/Routes';
import { CustomFormikProps } from 'app/types/Formik';
import { getSknadStepPath, isNextStepAvailable } from 'app/utils/stepUtils';
import { StepID } from 'app/types/SknadStep';
import Arbeidsforhold from '../arbeidsforhold/Arbeidsforhold';
import Intro from '../intro/Intro';
import Oppsummering from '../oppsummering/Oppsummering';
import SknadSendt from '../sknad-sendt/SknadSendt';
import Termin from '../termin/Termin';
import Tilrettelegging from '../tilrettelegging/Tilrettelegging';
import Utenlandsopphold from '../utenlandsopphold/Utenlandsopphold';

interface Props {
    harSendtSknad: boolean;
    formikProps: CustomFormikProps;
}

const SknadRoutes: FunctionComponent<Props> = ({ formikProps, harSendtSknad }) => {
    const { values } = formikProps;

    const isSknadAvailable = isNextStepAvailable({ path: AppRoute.INTRO }, values);
    const isNextAvailable = (step: StepID) => isNextStepAvailable({ path: AppRoute.SKNAD, step }, values);

    const tilretteleggingRoutes = values.tilrettelegging.map(({ id }) => {
        const sknadStep = {
            step: StepID.TILRETTELEGGING,
            subStep: id,
        };

        return (
            <Route
                path={getSknadStepPath(sknadStep.step, sknadStep.subStep)}
                key={`${StepID.TILRETTELEGGING}.${id}`}
                element={<Tilrettelegging id={id} step={sknadStep} formikProps={formikProps} />}
            />
        );
    });
    const sknadRoutes = (
        <Routes>
            <Route path={AppRoute.INTRO} element={<Intro formik={formikProps} />} />

            {isSknadAvailable && (
                <Route
                    path={getSknadStepPath(StepID.TERMIN)}
                    key={StepID.TERMIN}
                    element={<Termin step={{ step: StepID.TERMIN }} formikProps={formikProps} />}
                />
            )}

            {isNextAvailable(StepID.TERMIN) && (
                <Route
                    path={getSknadStepPath(StepID.ARBEIDSFORHOLD)}
                    key={StepID.ARBEIDSFORHOLD}
                    element={<Arbeidsforhold step={{ step: StepID.ARBEIDSFORHOLD }} formikProps={formikProps} />}
                />
            )}

            {isNextAvailable(StepID.ARBEIDSFORHOLD) && values.sknadsgrunnlag.length > 0 && tilretteleggingRoutes}

            {isNextAvailable(StepID.TILRETTELEGGING) && (
                <Route
                    path={getSknadStepPath(StepID.UTENLANDSOPPHOLD)}
                    key={StepID.UTENLANDSOPPHOLD}
                    element={<Utenlandsopphold step={{ step: StepID.UTENLANDSOPPHOLD }} formikProps={formikProps} />}
                />
            )}

            {isNextAvailable(StepID.UTENLANDSOPPHOLD) && (
                <Route
                    path={getSknadStepPath(StepID.OPPSUMMERING)}
                    key={StepID.OPPSUMMERING}
                    element={<Oppsummering step={{ step: StepID.OPPSUMMERING }} formikProps={formikProps} />}
                />
            )}

            <Route path="/" element={<Navigate to={AppRoute.INTRO} key="redirect" />} />
        </Routes>
    );

    const kvitteringRoute = (
        <Routes>
            <Route path={AppRoute.SENDT} element={<SknadSendt />} />
            <Route path="*" element={<Navigate to={AppRoute.SENDT} key="redirect" />} />
        </Routes>
    );

    return harSendtSknad ? kvitteringRoute : sknadRoutes;
};

export default SknadRoutes;
