import axios from 'axios';
import Environment from 'app/Environment';

const apiBaseUrl = Environment.REST_API_URL;
const sendSknadUrl = `${apiBaseUrl}/soknad`;

export function getAxiosInstance() {
    return axios;
}

export function getSkerinfo() {
    return axios.get(`${apiBaseUrl}/sokerinfo`, {
        timeout: 15 * 1000,
        withCredentials: true,
    });
}

export function sendSknad(sknad: any) {
    return axios.post(sendSknadUrl, sknad, {
        withCredentials: true,
        headers: {
            'content-type': 'application/json;',
        },
    });
}
