import { UferdigSknad, Sknadfeil } from 'app/types/Sknad';

const validateOppsummering = (sknad: UferdigSknad): Sknadfeil => {
    const errors: Sknadfeil = {};

    if (sknad.harGodkjentOppsummering !== true) {
        errors.harGodkjentOppsummering = 'valideringsfeil.oppsummeringMGodkjennes';
    }

    return errors;
};

export default validateOppsummering;
