import React, { FunctionComponent } from 'react';
import { CheckboksPanelGruppe, CheckboksPanelGruppeProps } from 'nav-frontend-skjema';
import { FieldArrayRenderProps, FieldArray } from 'formik';
import { Omit } from 'lodash';
import { Sknadsgrunnlag } from 'app/types/Sknad';
import { Arbeidsforholdstype } from 'app/types/Tilrettelegging';

export interface SknadsgrunnlagOption {
    value: string;
    type: Arbeidsforholdstype;
    label: string;
}

interface OwnProps {
    name: string;
    label: string;
    options: SknadsgrunnlagOption[];
}

type Props = OwnProps & Omit<CheckboksPanelGruppeProps, 'onChange' | 'checkboxes' | 'legend'>;

const VelgSknadsgrunnlag: FunctionComponent<Props> = (props) => {
    const { name, label, options } = props;

    return (
        <FieldArray
            name={name}
            render={({ form, push, remove }: FieldArrayRenderProps) => {
                return (
                    <CheckboksPanelGruppe
                        legend={label}
                        checkboxes={options.map((option) => ({
                            value: option.value,
                            label: option.label,
                            checked: form.values[name].some((v: Sknadsgrunnlag) => v.id === option.value),
                        }))}
                        onChange={(_, value) => {
                            const indexOfGrunnlag = form.values[name].findIndex((v: Sknadsgrunnlag) => v.id === value);
                            const matchingOption = options.find((o) => o.value === value);

                            if (matchingOption) {
                                if (indexOfGrunnlag === -1) {
                                    push({
                                        id: value,
                                        type: matchingOption.type,
                                    });
                                } else {
                                    remove(indexOfGrunnlag);
                                }
                            }
                        }}
                    />
                );
            }}
        />
    );
};

export default VelgSknadsgrunnlag;
