import { all, put, call, takeLatest } from 'redux-saga/effects';
import { getSkerinfo, sendSknad } from '../../api/api';
import { Skerinfo } from 'app/types/Skerinfo';
import Kvittering from 'app/types/Kvittering';
import { ApiActionTypes, GetSkerinfoRequest, SendSknadRequest } from '../types/ApiAction';
import normalizeName from 'app/utils/normalizeName';
import { SkerinfoDTOArbeidsforhold } from 'app/types/SkerinfoArbeidsforholdDTO';
import { mapArbeidsforhold } from 'app/utils/arbeidsforholdUtils';

function* getSkerInfoSaga(_: GetSkerinfoRequest): any {
    try {
        const response = yield call(getSkerinfo);
        const { sker } = response.data;
        const arbeidsforhold: SkerinfoDTOArbeidsforhold[] | undefined =
            response.data !== undefined ? response.data.arbeidsforhold : [];
        const skerinfo: Skerinfo = {
            ...response.data,
            arbeidsforhold: mapArbeidsforhold(arbeidsforhold),
            sker: {
                ...sker,
                fornavn: normalizeName(sker.fornavn),
                mellomnavn: sker.mellomnavn ? normalizeName(sker.mellomnavn) : undefined,
                etternavn: normalizeName(sker.etternavn),
            },
        };

        yield put({ type: ApiActionTypes.GET_SKERINFO_SUCCESS, payload: { skerinfo } });
    } catch (error) {
        yield put({ type: ApiActionTypes.GET_SKERINFO_FAILURE, payload: { error } });
    }
}

function* sendSknadSaga(action: SendSknadRequest): any {
    try {
        const response = yield call(sendSknad, action.payload.sknad);
        const kvittering: Kvittering = response.data;

        yield put({ type: ApiActionTypes.SEND_SKNAD_SUCCESS, payload: { kvittering } });
    } catch (error) {
        yield put({ type: ApiActionTypes.SEND_SKNAD_FAILURE, payload: { error } });
    }
}

function* apiSaga() {
    yield all([takeLatest(ApiActionTypes.GET_SKERINFO_REQUEST, getSkerInfoSaga)]);
    yield all([takeLatest(ApiActionTypes.SEND_SKNAD_REQUEST, sendSknadSaga)]);
}

export default apiSaga;
