import React, { FunctionComponent } from 'react';
import moment from 'moment';
import { Normaltekst, Element } from 'nav-frontend-typografi';
import BEMHelper from 'common/util/bem';

import { TidsperiodeMedValgfriSluttdato } from 'common/types';
import { Nringstype } from 'app/types/SelvstendigNringsdrivende';
import getMessage from 'common/util/i18nUtils';
import { FormattedMessage, useIntl } from 'react-intl';
import Block from 'common/components/block/Block';

import './detaljerSelvstendig.less';

interface Props {
    orgnr: string;
    navnPNringen: string;
    oppstartsdato: string;
    pgende: boolean;
    tidsperiode: Partial<TidsperiodeMedValgfriSluttdato>;
    typer: Nringstype[];
}

const cls = BEMHelper('detaljerSelvstendig');

const DetaljerSelvstendig: FunctionComponent<Props> = ({
    orgnr,
    navnPNringen,
    oppstartsdato,
    pgende,
    tidsperiode,
    typer,
}) => {
    const intl = useIntl();

    return (
        <div className={cls.block}>
            <Normaltekst>
                <FormattedMessage id="oppsummering.arbeidsforhold.svar.selvstendig.orgnr" /> {orgnr}
            </Normaltekst>
            <Element>{navnPNringen.toUpperCase()}</Element>
            <Block margin="xxs">
                <em>{typer.map((type) => getMessage(intl, `nringstype.${type.toLocaleLowerCase()}`))}</em>
            </Block>
            <Block margin="xxs">
                {moment(oppstartsdato).format('DD.MM.YYYY')} -{' '}
                {pgende ? <FormattedMessage id="pgende" /> : moment(tidsperiode.tom).format('DD.MM.YYYY')}
            </Block>
        </div>
    );
};

export default DetaljerSelvstendig;
