import { Nring, Nringstype } from 'app/types/SelvstendigNringsdrivende';
import { cleanupNring } from './cleanup';
import moment from 'moment';

export const visKomponentSelvstendigNringsdrivende = (nring: Partial<Nring>) => {
    const normalisertNring = cleanupNring(nring);
    const {
        nringstyper,
        navnPNringen,
        registrertINorge,
        organisasjonsnummer,
        registrertILand,
        tidsperiode = {},
        nringsinntekt,
        harBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene,
        oppstartsdato,
        harRegnskapsfrer,
        regnskapsfrer,
        revisor,
        harRevisor,
        endringAvNringsinntektInformasjon,
        kanInnhenteOpplsyningerFraRevisor,
        hattVarigEndringAvNringsinntektSiste4Kalenderr,
    } = normalisertNring;

    const skalViseNavnPNringen = nringstyper !== undefined && nringstyper.length! > 0;
    const skalViseAdvarselFisker =
        skalViseNavnPNringen && (nringstyper as Nringstype[]).includes(Nringstype.FISKER);
    const skalViseRegistrertINorge = skalViseNavnPNringen && navnPNringen !== undefined && navnPNringen !== '';
    const skalViseLand = skalViseRegistrertINorge && registrertINorge === false;
    const skalViseOrgNr = skalViseRegistrertINorge && registrertINorge === true;
    const skalViseTidsperiode =
        (skalViseLand || skalViseOrgNr) &&
        ((registrertINorge === true && organisasjonsnummer !== undefined && organisasjonsnummer !== '') ||
            registrertILand !== undefined);
    const skalVisevarigEndringAvNringsinntektBolk =
        tidsperiode !== undefined &&
        tidsperiode.fom !== undefined &&
        tidsperiode.fom !== '' &&
        moment(tidsperiode.fom).isBefore(moment().subtract(4, 'year'));
    const skalViseNringsinntekt =
        skalViseTidsperiode && !skalVisevarigEndringAvNringsinntektBolk && tidsperiode.fom !== undefined;
    const skalViseharBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene =
        skalViseNringsinntekt &&
        !skalVisevarigEndringAvNringsinntektBolk &&
        nringsinntekt !== undefined &&
        nringsinntekt !== '';
    const skalViseOppstartsdato =
        skalViseharBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene &&
        !skalVisevarigEndringAvNringsinntektBolk &&
        harBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene === true;
    const skalViseHarRegnskapsfrer =
        (skalViseharBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene &&
            oppstartsdato !== undefined &&
            oppstartsdato !== '') ||
        (skalViseharBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene &&
            harBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene === false) ||
        (skalVisevarigEndringAvNringsinntektBolk && hattVarigEndringAvNringsinntektSiste4Kalenderr === false) ||
        (endringAvNringsinntektInformasjon !== undefined &&
            endringAvNringsinntektInformasjon.dato !== undefined &&
            endringAvNringsinntektInformasjon.dato !== '' &&
            endringAvNringsinntektInformasjon.nringsinntektEtterEndring !== undefined &&
            endringAvNringsinntektInformasjon.forklaring !== undefined &&
            endringAvNringsinntektInformasjon.forklaring !== '');
    const skalViseNringsrelasjonRegnskapsfrer = skalViseHarRegnskapsfrer && harRegnskapsfrer === true;
    const skalViseRevisor = skalViseHarRegnskapsfrer && harRegnskapsfrer === false;
    const skalViseNringsrelasjonRevisor = skalViseRevisor && harRevisor === true;
    const skalViseKanInnhenteOpplysningerFraRevisor =
        skalViseNringsrelasjonRevisor && revisor !== undefined && revisor.erNrVennEllerFamilie !== undefined;
    const skalViseformButtons =
        (harRegnskapsfrer === false && harRevisor === false && skalViseRevisor) ||
        (harRegnskapsfrer === true &&
            skalViseNringsrelasjonRegnskapsfrer &&
            regnskapsfrer !== undefined &&
            regnskapsfrer.navn !== undefined &&
            regnskapsfrer.navn !== '' &&
            regnskapsfrer.telefonnummer !== undefined &&
            regnskapsfrer.telefonnummer !== '' &&
            regnskapsfrer.erNrVennEllerFamilie !== undefined) ||
        (harRevisor === true &&
            skalViseNringsrelasjonRevisor &&
            revisor !== undefined &&
            revisor.navn !== undefined &&
            revisor.navn !== '' &&
            revisor.telefonnummer !== undefined &&
            revisor.telefonnummer !== '' &&
            revisor.erNrVennEllerFamilie !== undefined &&
            kanInnhenteOpplsyningerFraRevisor !== undefined);

    return {
        skalViseNavnPNringen,
        skalViseAdvarselFisker,
        skalViseRegistrertINorge,
        skalViseLand,
        skalViseOrgNr,
        skalViseTidsperiode,
        skalVisevarigEndringAvNringsinntektBolk,
        skalViseNringsinntekt,
        skalViseharBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene,
        skalViseOppstartsdato,
        skalViseHarRegnskapsfrer,
        skalViseNringsrelasjonRegnskapsfrer,
        skalViseRevisor,
        skalViseNringsrelasjonRevisor,
        skalViseKanInnhenteOpplysningerFraRevisor,
        skalViseformButtons,
    };
};
