import React, { FunctionComponent } from 'react';
import { Nringsrelasjon } from 'app/types/SelvstendigNringsdrivende';
import { Element } from 'nav-frontend-typografi';

import './regnskapsfrerSelvstendig.less';
import BEMHelper from 'common/util/bem';
import { FormattedMessage } from 'react-intl';
import Block from 'common/components/block/Block';

const cls = BEMHelper('regnskapsfrerSelvstendig.less');

interface Props {
    regnskapsfrer: Nringsrelasjon;
}

const RegnskapsfrerSelvstendig: FunctionComponent<Props> = ({ regnskapsfrer }) => {
    return (
        <div className={cls.block}>
            <div>
                <Element>
                    <FormattedMessage id="oppsummering.arbeidsforhold.svar.selvstendig.regnskapsfrer" />
                </Element>
            </div>
            <Block margin="xxs">
                <FormattedMessage
                    id="oppsummering.arbeidsforhold.svar.selvstendig.regnskapsfrer.navn"
                    values={{ navn: regnskapsfrer.navn }}
                />
            </Block>
            <Block margin="xxs">
                <FormattedMessage
                    id="oppsummering.arbeidsforhold.svar.selvstendig.regnskapsfrer.tlf"
                    values={{ navn: regnskapsfrer.telefonnummer }}
                />
            </Block>
            <Block margin="xxs">
                <FormattedMessage id="oppsummering.arbeidsforhold.svar.selvstendig.regnskapsfrer.vennEllerFamilie" />
            </Block>
        </div>
    );
};

export default RegnskapsfrerSelvstendig;
