import React, { FunctionComponent, ReactElement } from 'react';
import { connect } from 'react-redux';
import DocumentTitle from 'react-document-title';
import { FormattedMessage } from 'react-intl';

import { CommonActionTypes } from 'app/redux/types/CommonAction';
import { Sprkkode } from 'common/types';
import { State } from 'app/redux/store';
import Action from 'app/redux/types/Action';
import Sknadstittel from 'app/components/sknadstittel/Sknadstittel';
import LanguageToggle from 'common/components/language-toggle/LanguageToggle';

interface OwnProps {
    visSprkvelger?: boolean;
    visTittel?: boolean;
    children: ReactElement | ReactElement[];
}

interface StateProps {
    sprkkode: Sprkkode;
}

interface DispatchProps {
    setSprk: (sprkkode: Sprkkode) => void;
}

type Props = OwnProps & StateProps & DispatchProps;

const Applikasjonsside: FunctionComponent<Props> = ({ visSprkvelger, visTittel, sprkkode, setSprk, children }) => {
    return (
        <>
            <DocumentTitle title="Svangerskapspengesknad" />
            {visSprkvelger && (
                <LanguageToggle
                    language={sprkkode}
                    // @ts-ignore Fiks
                    toggleLanguage={(languageCode: Sprkkode) => setSprk(languageCode)}
                />
            )}
            {visTittel && (
                <Sknadstittel>
                    <FormattedMessage id="app.banner" />
                </Sknadstittel>
            )}
            {children}
        </>
    );
};

const mapStateToProps = (state: State): StateProps => ({
    sprkkode: state.common.sprkkode,
});

const mapDispatchToProps = (dispatch: (action: Action) => void): DispatchProps => ({
    setSprk: (sprkkode: Sprkkode) => dispatch({ type: CommonActionTypes.SET_SPRK, payload: { sprkkode } }),
});

export default connect(mapStateToProps, mapDispatchToProps)(Applikasjonsside);
