import React, { FunctionComponent } from 'react';
import { CustomFormikProps } from 'app/types/Formik';
import { getSknadStepPath } from 'app/utils/stepUtils';
import { useIntl } from 'react-intl';
import Applikasjonsside from '../applikasjonsside/Applikasjonsside';
import Block from 'common/components/block/Block';
import DatoInput from 'app/formik/wrappers/DatoInput';
import FormikStep from 'app/components/formik-step/FormikStep';
import getMessage from 'common/util/i18nUtils';
import JaNeiSprsml from 'app/formik/wrappers/JaNeiSprsml';
import SknadStep, { StepID } from 'app/types/SknadStep';
import {
    dateToISOFormattedDateString,
    etrSiden,
    niMnederFremITid,
    tiMnederSiden,
} from '../../../common/util/datoUtils';
import { useNavigate } from 'react-router-dom';

interface OwnProps {
    step: SknadStep;
    formikProps: CustomFormikProps;
}

type Props = OwnProps;

const Termin: FunctionComponent<Props> = (props) => {
    const { step, formikProps } = props;
    const { values } = formikProps;

    const intl = useIntl();

    const navigateTo = useNavigate();

    const navigate = () => {
        navigateTo(getSknadStepPath(StepID.ARBEIDSFORHOLD));
    };

    const showNesteknapp = values.barn.erBarnetFdt === false || values.barn.fdselsdato !== undefined;

    return (
        <Applikasjonsside visTittel={true} visSprkvelger={true}>
            <FormikStep
                step={step}
                showNesteknapp={showNesteknapp}
                formikProps={formikProps}
                onValidFormSubmit={navigate}
            >
                <Block>
                    <DatoInput
                        name="barn.termindato"
                        label={getMessage(intl, 'termin.termindato')}
                        datoAvgrensinger={{
                            minDato: etrSiden(new Date()),
                            maksDato: niMnederFremITid(new Date()),
                        }}
                    />
                </Block>
                <Block visible={values.barn.termindato !== undefined}>
                    <JaNeiSprsml name="barn.erBarnetFdt" legend={getMessage(intl, 'termin.erBarnetFdt')} />
                </Block>
                <Block visible={values.barn.erBarnetFdt === true}>
                    <DatoInput
                        name="barn.fdselsdato"
                        label={getMessage(intl, 'termin.fdselsdato')}
                        datoAvgrensinger={{
                            minDato: tiMnederSiden(values.barn.termindato!),
                            maksDato: dateToISOFormattedDateString(new Date()),
                        }}
                    />
                </Block>
            </FormikStep>
        </Applikasjonsside>
    );
};

export default Termin;
