import { FormikErrors } from 'formik';
import { Nring } from 'app/types/SelvstendigNringsdrivende';
import { erGyldigNorskOrgnummer } from './utils/organisasjonsnummer';
import Valideringsfeil from 'app/types/Valideringsfeil';
import { set } from 'lodash';

type SelvstendigNringsdrivendeFeil = FormikErrors<Nring>;

const validateSelvstendigNringsdrivende =
    () =>
    (nring: Partial<Nring>): SelvstendigNringsdrivendeFeil => {
        const errors: SelvstendigNringsdrivendeFeil = {};

        if (nring.navnPNringen && nring.navnPNringen.length > 100) {
            errors.navnPNringen = 'valideringsfeil.feltetKanVreMax100Tegn';
        }

        if (nring.organisasjonsnummer && !erGyldigNorskOrgnummer(nring.organisasjonsnummer)) {
            errors.organisasjonsnummer =
                nring.organisasjonsnummer.length !== 9
                    ? 'valideringsfeil.mBestAv9Siffer'
                    : 'valideringsfeil.ugyldigOrganisasjonsnummer';
        }

        if (
            (nring.nringsinntekt && isNaN(Number(nring.nringsinntekt))) ||
            (nring.nringsinntekt && (nring.nringsinntekt.includes(',') || nring.nringsinntekt.includes('.')))
        ) {
            errors.nringsinntekt = Valideringsfeil.M_VRE_HELTALL;
        }

        if (nring.revisor) {
            const revisor = nring.revisor;
            if (revisor.navn && revisor.navn.length > 100) {
                set(errors, ['revisor', 'navn'], 'valideringsfeil.feltetKanVreMax100Tegn');
            }
            if (revisor.navn && revisor.telefonnummer.length > 100) {
                set(errors, ['revisor', 'telefonnummer'], 'valideringsfeil.feltetKanVreMax100Tegn');
            }
        }

        if (nring.regnskapsfrer) {
            const regnskapsfrer = nring.regnskapsfrer;
            if (regnskapsfrer.navn && regnskapsfrer.navn.length > 100) {
                set(errors, ['regnskapsfrer', 'navn'], 'valideringsfeil.feltetKanVreMax100Tegn');
            }
            if (regnskapsfrer.navn && regnskapsfrer.telefonnummer.length > 100) {
                set(errors, ['regnskapsfrer', 'telefonnummer'], 'valideringsfeil.feltetKanVreMax100Tegn');
            }
        }

        if (nring.endringAvNringsinntektInformasjon) {
            if (
                nring.endringAvNringsinntektInformasjon.forklaring &&
                nring.endringAvNringsinntektInformasjon.forklaring.length > 1000
            ) {
                set(
                    errors,
                    ['endringAvNringsinntektInformasjon', 'forklaring'],
                    'valideringsfeil.feltetKanVreMax1000Tegn'
                );
            }

            const nringsinntektEtterEndring = nring.endringAvNringsinntektInformasjon.nringsinntektEtterEndring;
            if (
                (nringsinntektEtterEndring && isNaN(Number(nringsinntektEtterEndring))) ||
                (nringsinntektEtterEndring &&
                    (nringsinntektEtterEndring.includes(',') || nringsinntektEtterEndring.includes('.')))
            ) {
                set(
                    errors,
                    ['endringAvNringsinntektInformasjon', 'nringsinntektEtterEndring'],
                    Valideringsfeil.M_VRE_HELTALL
                );
            }
        }

        return errors;
    };
export default validateSelvstendigNringsdrivende;
