import { Sker } from 'app/types/Sker';
import * as _ from 'lodash';
import { FrilansInformasjon } from 'app/types/FrilansInformasjon';
import { DeepPartial } from 'redux';
import { Nring } from 'app/types/SelvstendigNringsdrivende';
import moment from 'moment';
import { AnnenInntekt, AnnenInntektType } from 'app/types/AnnenInntekt';

export const cleanupFrilansinformasjon = (sker: Partial<Sker>): DeepPartial<FrilansInformasjon> => {
    const { harJobbetSomFrilansSiste10Mnd, frilansInformasjon } = sker;

    let relevanteFelter: string[] = [];
    if (harJobbetSomFrilansSiste10Mnd && frilansInformasjon) {
        relevanteFelter = [
            'jobberFremdelesSomFrilans',
            'oppstart',
            'driverFosterhjem',
            'harJobbetForNrVennEllerFamilieSiste10Mnd',
        ];

        if (frilansInformasjon.oppdragForNreVennerEllerFamilieSiste10Mnd) {
            relevanteFelter.push('oppdragForNreVennerEllerFamilieSiste10Mnd');
        }
    }

    return _.pick(frilansInformasjon, relevanteFelter);
};

export const cleanupNring = (nring: Partial<Nring>): DeepPartial<Nring> => {
    const relevanteFelter: string[] = [
        'nringstyper',
        'navnPNringen',
        'registrertINorge',
        'harBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene',
        'tidsperiode',
        'hattVarigEndringAvNringsinntektSiste4Kalenderr',
        'harRevisor',
        'harRegnskapsfrer',
    ];

    nring.registrertINorge === true
        ? relevanteFelter.push('organisasjonsnummer')
        : relevanteFelter.push('registrertILand');

    nring.tidsperiode &&
    nring.tidsperiode.fom !== undefined &&
    moment(nring.tidsperiode.fom).isBefore(moment().subtract(4, 'year'))
        ? relevanteFelter.push('endringAvNringsinntektInformasjon')
        : relevanteFelter.push(
              'nringsinntekt',
              'oppstartsdato',
              'harBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene'
          );

    if (nring.harRegnskapsfrer === true) {
        relevanteFelter.push('regnskapsfrer');
    } else if (nring.harRevisor === true) {
        relevanteFelter.push('revisor', 'kanInnhenteOpplsyningerFraRevisor');
    }
    return _.pick(nring, relevanteFelter);
};

export const cleanupSker = (sker: Partial<Sker>) => {
    const relevanteFelter: string[] = [
        'rolle',
        'harJobbetSomFrilansSiste10Mnd',
        'harJobbetSomSelvstendigNringsdrivendeSiste10Mnd',
        'harHattAnnenInntektSiste10Mnd',
    ];

    if (sker.harJobbetSomFrilansSiste10Mnd) {
        relevanteFelter.push('frilansInformasjon');
    }

    if (sker.harJobbetSomSelvstendigNringsdrivendeSiste10Mnd) {
        relevanteFelter.push('selvstendigNringsdrivendeInformasjon');
    }

    if (sker.harHattAnnenInntektSiste10Mnd) {
        relevanteFelter.push('andreInntekterSiste10Mnd');
    }

    return _.pick(sker, relevanteFelter);
};

export const cleanupAnnenInntekt = (annenInntekt: Partial<AnnenInntekt>): AnnenInntekt => {
    annenInntekt.vedlegg = annenInntekt.type === AnnenInntektType.MILITRTJENESTE ? annenInntekt.vedlegg : [];
    return annenInntekt as AnnenInntekt;
};
