import React, { FunctionComponent } from 'react';
import moment from 'moment';
import { EndringAvNringsinntektInformasjon } from 'app/types/SelvstendigNringsdrivende';
import { Element } from 'nav-frontend-typografi';
import BEMHelper from 'common/util/bem';
import './endringSelvstendig.less';
import OppsummeringBeskrivelse from '../../../OppsummeringBeskrivelse';
import { FormattedMessage, useIntl } from 'react-intl';
import Block from 'common/components/block/Block';
import getMessage from 'common/util/i18nUtils';

interface Props {
    endringAvNringsinntektInformasjon: EndringAvNringsinntektInformasjon;
}

const cls = BEMHelper('endringSelvstendig');

const EndringSelvstendig: FunctionComponent<Props> = ({ endringAvNringsinntektInformasjon }) => {
    const intl = useIntl();
    return (
        <div className={cls.block}>
            <div>
                <Element>
                    <FormattedMessage id="oppsummering.arbeidsforhold.svar.selvstendig.endring" />
                </Element>
            </div>
            <Block margin="xxs">{moment(endringAvNringsinntektInformasjon.dato).format('DD.MM.YYYY')}</Block>
            <Block margin="xxs">
                <FormattedMessage id="oppsummering.arbeidsforhold.svar.selvstendig.etterEndring" />{' '}
                {endringAvNringsinntektInformasjon.nringsinntektEtterEndring}
            </Block>
            <Block margin="xxs">
                <OppsummeringBeskrivelse
                    label={getMessage(intl, 'oppsummering.arbeidsforhold.svar.selvstendig.beskrivelseAvEndring')}
                    innhold={endringAvNringsinntektInformasjon.forklaring}
                />
            </Block>
        </div>
    );
};

export default EndringSelvstendig;
