import React, { FunctionComponent } from 'react';
import { Person } from 'app/types/Skerinfo';
import BEMHelper from 'common/util/bem';
import Block from 'common/components/block/Block';
import SpotlightLetter from 'common/components/ikoner/SpotlightLetter';
import { Sidetittel, Normaltekst } from 'nav-frontend-typografi';
import { FormattedMessage } from 'react-intl';
import moment from 'moment';
import { dateToHours } from 'app/utils/formatDate';

import Lenke from 'nav-frontend-lenker';
import { openPdfPreview } from '../util/pdfUtils';

import './kvitteringHeader.less';

interface Props {
    sker: Person;
    mottattDato: string;
    pdf: string;
}

const cls = BEMHelper('kvitteringHeader');

const KvitteringHeader: FunctionComponent<Props> = ({ sker, pdf }) => {
    return (
        <div className={cls.block}>
            <Block margin="m">
                <SpotlightLetter className={cls.element('spotlightLetter')} />
            </Block>

            <Block margin="s">
                <Sidetittel tag="h4">
                    <FormattedMessage
                        id="sknadSendt.tittel"
                        values={{
                            name: `${sker.fornavn} ${sker.etternavn}`,
                        }}
                    />
                </Sidetittel>
            </Block>

            <Block visible={pdf !== undefined}>
                <Lenke
                    href={'#'}
                    onClick={(e) => {
                        e.preventDefault();
                        openPdfPreview(pdf);
                    }}
                >
                    <FormattedMessage id={'sknadSendt.pdf'} />
                </Lenke>
            </Block>

            <Block>
                <div className={cls.element('sendtInnTid')}>
                    <Normaltekst>
                        <FormattedMessage id="sknadSendt.sendtInn" />
                    </Normaltekst>
                    <span style={{ width: '0.25rem' }} />
                    {moment().format('Do MMMM YYYY')}, kl. {dateToHours(moment().toDate())}
                </div>
            </Block>
        </div>
    );
};

export default KvitteringHeader;
