import React, { FunctionComponent, useMemo } from 'react';
import { FormattedMessage, useIntl } from 'react-intl';
import { Formik, FormikProps } from 'formik';
import { Undertittel } from 'nav-frontend-typografi';
import { Knapp, Hovedknapp } from 'nav-frontend-knapper';
import _ from 'lodash';
import { isValid } from 'i18n-iso-countries';

import BEMHelper from 'common/util/bem';
import { Nringstype, Nring } from 'app/types/SelvstendigNringsdrivende';
import getMessage from 'common/util/i18nUtils';
import CheckboksPanelGruppe from 'app/formik/wrappers/CheckboksPanelGruppe';
import Block from 'common/components/block/Block';
import InputField from 'app/formik/wrappers/InputField';
import JaNeiSprsml from 'app/formik/wrappers/JaNeiSprsml';
import getCountries from 'app/utils/getCountries';
import DatoInput from 'app/formik/wrappers/DatoInput';
import Veilederinfo from 'common/components/veileder-info/Veilederinfo';
import VarigEndringAvNringsinntekt from './VarigEndringAvNringsinntekt';
import Nringsrelasjon from './Nringsrelasjon';
import { ModalFormProps } from '../ArbeidSeksjon/ArbeidSeksjon';
import Select from 'app/formik/wrappers/Select';
import validateSelvstendigNringsdrivende from 'app/utils/validation/validateSelvstendigNringsdrivende';
import { visKomponentSelvstendigNringsdrivende } from '../utils/visibility';
import { cleanupNring } from '../utils/cleanup';
import DatoerInputLayout from 'common/components/layout/datoerInputLayout/DatoerInputLayout';
import Knapperad from 'common/components/knapperad/Knapperad';

const cls = BEMHelper('selvstendig-nringsdrivende');

type Props = ModalFormProps<Nring>;
const SelvstendigNringsdrivende: FunctionComponent<Props> = (props: Props) => {
    const intl = useIntl();
    const { endre, onCancel, element = { nringstyper: [] }, onAdd } = props;
    const countries = useMemo(() => getCountries(true, true, intl.locale), []);
    const onSubmit = (nring: Nring) => {
        onAdd(cleanupNring(nring) as Nring);
    };

    return (
        <Formik
            // @ts-ignore Fiks
            initialValues={element}
            validate={validateSelvstendigNringsdrivende()}
            onSubmit={onSubmit}
            render={({ handleSubmit, values }: FormikProps<Nring>) => {
                const visKomponent = visKomponentSelvstendigNringsdrivende(values);
                return (
                    <form
                        className={cls.block}
                        onSubmit={(e: React.FormEvent<HTMLFormElement>) => {
                            e.preventDefault();
                            e.stopPropagation();
                            handleSubmit();
                        }}
                    >
                        <Undertittel className="title">
                            {getMessage(intl, `arbeidsforhold.selvstendig.tittel${endre ? '.endre' : ''}`)}
                        </Undertittel>

                        <Block>
                            <CheckboksPanelGruppe
                                name="nringstyper"
                                label={getMessage(intl, 'arbeidsforhold.selvstendig.nringstype')}
                                options={Object.values(Nringstype).map((nringstype: Nringstype) => ({
                                    label: getMessage(intl, `nringstype.${nringstype.toLocaleLowerCase()}`),
                                    value: nringstype,
                                }))}
                                columns={2}
                            />
                        </Block>

                        <Block visible={visKomponent.skalViseNavnPNringen}>
                            <InputField
                                name="navnPNringen"
                                label={getMessage(intl, 'arbeidsforhold.selvstendig.navn')}
                                required={true}
                            />
                        </Block>

                        <Block visible={visKomponent.skalViseAdvarselFisker}>
                            <Veilederinfo type="advarsel">
                                <FormattedMessage
                                    id="arbeidsforhold.selvstendig.fisker"
                                    values={{ navnPNringen: values.navnPNringen }}
                                />
                            </Veilederinfo>
                        </Block>

                        <Block visible={visKomponent.skalViseRegistrertINorge}>
                            <JaNeiSprsml
                                name="registrertINorge"
                                legend={getMessage(intl, 'arbeidsforhold.selvstendig.registrertINorge', {
                                    navn: values.navnPNringen,
                                })}
                            />
                        </Block>

                        <Block visible={visKomponent.skalViseLand}>
                            <Select
                                name="registrertILand"
                                label={getMessage(intl, 'arbeidsforhold.seslvstendig.registrertILand')}
                            >
                                <option value="" />
                                {countries.map((countryOption: string[]) => {
                                    const [countryCode, countryName] = countryOption;
                                    return (
                                        <option key={countryCode} value={countryCode}>
                                            {countryName}
                                        </option>
                                    );
                                })}
                            </Select>
                        </Block>

                        <Block visible={visKomponent.skalViseOrgNr}>
                            <InputField
                                name="organisasjonsnummer"
                                label={getMessage(intl, 'arbeidsforhold.selvstendig.organisasjonsnummer')}
                                required={true}
                            />
                        </Block>

                        <Block visible={visKomponent.skalViseTidsperiode}>
                            <DatoerInputLayout
                                fullbredde={true}
                                fra={
                                    <DatoInput
                                        fullskjermKalender={true}
                                        name="tidsperiode.fom"
                                        label={getMessage(intl, 'arbeidsforhold.selvstendig.fom', {
                                            navn: values.navnPNringen,
                                        })}
                                    />
                                }
                                til={
                                    <DatoInput
                                        fullskjermKalender={true}
                                        name="tidsperiode.tom"
                                        label={getMessage(intl, 'arbeidsforhold.selvstendig.tom', {
                                            navn: values.navnPNringen,
                                        })}
                                    />
                                }
                            />
                        </Block>

                        <Block visible={visKomponent.skalVisevarigEndringAvNringsinntektBolk}>
                            <VarigEndringAvNringsinntekt values={values} />
                        </Block>

                        <Block visible={visKomponent.skalViseNringsinntekt}>
                            <InputField
                                name="nringsinntekt"
                                label={getMessage(intl, 'arbeidsforhold.selvstendig.nringsinntekt')}
                                required={true}
                            />
                        </Block>

                        <Block visible={visKomponent.skalViseharBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene}>
                            <JaNeiSprsml
                                name="harBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene"
                                legend={getMessage(
                                    intl,
                                    'arbeidsforhold.selvstendig.harBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene'
                                )}
                            />
                        </Block>

                        <Block visible={visKomponent.skalViseOppstartsdato}>
                            <DatoInput
                                fullskjermKalender={true}
                                name="oppstartsdato"
                                label={getMessage(intl, 'arbeidsforhold.selvstendig.oppstartsdato')}
                            />
                        </Block>

                        <Block visible={visKomponent.skalViseHarRegnskapsfrer}>
                            <JaNeiSprsml
                                name="harRegnskapsfrer"
                                legend={getMessage(intl, 'arbeidsforhold.selvstendig.harRegnskapsfrer')}
                            />
                        </Block>

                        <Block visible={visKomponent.skalViseNringsrelasjonRegnskapsfrer}>
                            <Nringsrelasjon type="regnskapsfrer" values={values} />
                        </Block>

                        <Block visible={visKomponent.skalViseRevisor}>
                            <JaNeiSprsml
                                name="harRevisor"
                                legend={getMessage(intl, 'arbeidsforhold.selvstendig.harRevisor')}
                            />
                        </Block>
                        <Block visible={visKomponent.skalViseNringsrelasjonRevisor}>
                            <Nringsrelasjon type="revisor" values={values} />
                        </Block>

                        <Block visible={visKomponent.skalViseKanInnhenteOpplysningerFraRevisor}>
                            <JaNeiSprsml
                                name="kanInnhenteOpplsyningerFraRevisor"
                                legend={getMessage(
                                    intl,
                                    'arbeidsforhold.selvstendig.kanInnhenteOpplsyningerFraRevisor'
                                )}
                            />
                        </Block>

                        <Block visible={visKomponent.skalViseformButtons}>
                            <Veilederinfo type="advarsel">
                                <FormattedMessage id="arbeidsforhold.selvstendig.bliKontaktet" />
                            </Veilederinfo>
                        </Block>

                        <Block visible={visKomponent.skalViseformButtons}>
                            <Knapperad stil="mobile-50-50">
                                <Knapp htmlType="button" onClick={onCancel}>
                                    <FormattedMessage id="avbryt" />
                                </Knapp>
                                <Hovedknapp disabled={!isValid} htmlType="submit">
                                    <FormattedMessage id={endre ? 'endre' : 'leggtil'} />
                                </Hovedknapp>
                            </Knapperad>
                        </Block>
                    </form>
                );
            }}
        />
    );
};

export default SelvstendigNringsdrivende;
